import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { PersonalService } from '../../../servicios/persona.service';
import { Personal } from '../../../models/personal';
import { ToastrService } from 'ngx-toastr';
import {
  AngularFireStorage
} from 'angularfire2/storage';
import * as firebase from 'firebase/app';
declare var $: any;

import { element } from 'protractor';
import { isObject } from 'util';
import { NgForm } from '@angular/forms';
import { async } from '@angular/core/testing';
import { finalize } from 'rxjs/operators';
import { IMPLICIT_REFERENCE } from '@angular/compiler/src/render3/view/util';
import { Observable } from 'rxjs';
import * as jspdf from 'jspdf';
import { autoTable } from 'jspdf-autotable';
import 'jspdf-autotable';
import { AuthService } from '../../../servicios/ser-auth/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-personal-list',
  templateUrl: './personal-list.component.html',
  styleUrls: ['./personal-list.component.css']
})
export class PersonalListComponent implements OnInit {

  uploadPercent: Observable<number>;
  urlImage: Observable<string>;


  personaList: Personal[];
  nombreFoto1: any;
  file: any;
  filePath: any;
  selectedImage: any;
  ref: any;
  img: any;
  imgSrc: any;

  @ViewChild('fileUploader', { static: false }) fileUploader: ElementRef;

  constructor(
    private personalService: PersonalService,
    private toastr: ToastrService,
    private storage: AngularFireStorage,
    private authService: AuthService,
    private router: Router
  ) {
  }

  filterPost = '';
  onSubmit(personalForm: NgForm) {

    // recuperamos la url del input para guardar en la base de dato
    this.img = (document.getElementById('urlImageValue') as HTMLInputElement).value;

    if (personalForm.value.$key == null) {
      this.personalService.selectedPersonal.perfil = this.img;
      personalForm.value.perfil = this.img;
      this.personalService.insertPersonal(personalForm.value);
      this.toastr.success('Operacion Correcta', 'Personal registrado correctamente');
    } else {
      if (this.img != null) {
        this.personalService.selectedPersonal.perfil = this.img;
        personalForm.value.perfil = this.img;
      }
      this.personalService.updatePersonal(personalForm.value);

      this.toastr.success('Operacion Correcta', 'Personal modificado correctamente');
    }
    this.resetForm(personalForm);
    $('#exampleModalScrollable').modal('hide');
    this.fileUploader.nativeElement.value = "";
  }

  resetForm(personalForm?: NgForm) {
    if (personalForm != null) {
      personalForm.reset();
    }
    this.personalService.selectedPersonal = new Personal();
    this.imgSrc = '';
  }

  nivel = true;
  ngOnInit() {
    if (this.authService.correo === null || this.authService.correo === undefined) {
      this.router.navigate(['/autentificacion']);
    } else {
      let refUsuario = firebase.database().ref('Usuario');
      refUsuario.orderByChild('correo').equalTo(this.authService.correo).on('child_added', snap => {
        if (snap.val().nivel === 'Cliente')
          // console.log('cliente');
          this.nivel = false;
      })
    }
    // if(this.authService.correo === null  || this.authService.correo === undefined){
    //   this.router.navigate(['/autentificacion']);
    // }
    this.personalService.getPersonal()
      .snapshotChanges()
      .subscribe(item => {
        this.personaList = [];

        // tslint:disable-next-line: no-shadowed-variable
        item.forEach(element => {
          const x = element.payload.toJSON();
          // tslint:disable-next-line: no-string-literal
          x['$key'] = element.key;
          this.personaList.push(x as Personal);
          // });
          // this.personaList.push(x as Personal);
          // var pathRef = storage.ref(x["perfil"])
          //   // pathRef.child('images/stars.jpg').getDownloadURL().then(function(url) {
          //   pathRef.getDownloadURL().then(function (url) {
          //     console.log('url: ' + url);
          //     var img = document.getElementById('myimg');
          //     img.setAttribute("src",url)
          //   });
        });
      });
  }
  per: any;
  onEdit(persona: Personal) {
    this.imgSrc = persona.perfil;
    this.personalService.selectedPersonal = Object.assign({}, persona);
  }

  onDelete($key: string) {
    let img;
    if (confirm('Esta seguro que desea eliminar?...')) {
      let refImg = firebase.database().ref('Personal');
      refImg.orderByKey().equalTo($key).once('child_added', snap => {
        img = (snap.val().perfil).substring(89, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/personal/' + img2[0]).delete();
        this.personalService.deletePersonal($key);
        this.toastr.success('Operacion Correcta', 'Personal eliminado correctamente');
      })
    }
  }

  uploadFile1(event: any) {
    // limpiamos la muestra de la imagen pequeña
    this.imgSrc = '';
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.cargandoImagen(event);

    } else {
      this.cargandoImagen(event);
      if (this.personalService.selectedPersonal.perfil != undefined) {
        let img = (this.personalService.selectedPersonal.perfil).substring(89, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/personal/' + img2[0]).delete();
      }
    }
  }

  cargandoImagen(event: any) {
    // nombre aleatorio para la imagen
    const id = Math.random().toString(36).substring(2);
    this.file = event.target.files[0];
    this.filePath = 'Upload/personal/perfilPersonal_' + id;
    this.ref = this.storage.ref(this.filePath);
    const task = this.storage.upload(this.filePath, this.file);
    this.uploadPercent = task.percentageChanges();
    task.snapshotChanges().pipe(finalize(() => this.urlImage = this.ref.getDownloadURL())).subscribe();
    // cargar muestra de imagen pequeña
    const reader = new FileReader();
    reader.onload = (e: any) => this.imgSrc = e.target.result;
    reader.readAsDataURL(event.target.files[0]);
    this.selectedImage = event.target.files[0];
  }

  generarReporte() {
    // var div = document.getElementById("tabla")
    var pdf = new jspdf({
      orientation: 'landscape',
      // unit: 'cm',
      format: 'carta'
    })
    let imgData = 'data:image/jpeg;base64,'
    // pdf.addImage(imgData, 'PNG', 7, 0, 38, 33);
    pdf.text("LISTA DE PERSONAL", (279.4 / 2.5), 13);
    // pdf.fromHTML(div, 10, 30);
    pdf.autoTable({
      margin: { top: 25 },
      head: [[
        { content: 'Nombre', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Apellido', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'C.I.', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Expedido', styles: { halign: 'center', cellWidth: 25 } },
        { content: 'Fecha N.', styles: { halign: 'center', cellWidth: 23 } },
        { content: 'Telefono', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Direccion', styles: { halign: 'center', cellWidth: 45 } },
        { content: 'Sexo', styles: { halign: 'center', cellWidth: 25 } },
        { content: 'Cargo', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Correo', styles: { halign: 'center', cellWidth: 48 } }
      ]]
    })
    var ref = firebase.database().ref('Personal');
    ref.orderByChild('nombre').on('value', snap => {
      snap.forEach(element => {
        // console.log(element.val().nombre);

        pdf.autoTable({
          // head: [['Nombre', 'Apellido', 'C.I.', 'Expedido', 'Fecha N.', 'Telefono', 'Direccion', 'Sexo', 'Cargo', 'Correo']],
          // head: [['Nombre']],//,'Apellido','C.I.','Fecha N.','Telefono','Direccion','Sexo','Cargo','Correo']],
          body: [
            [
              { content: element.val().nombre, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().apellido, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().ci, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().expedicion, styles: { halign: 'center', cellWidth: 25 } },
              { content: element.val().fechaNacimiento, styles: { halign: 'center', cellWidth: 23 } },
              { content: element.val().telefono, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().direccion, styles: { halign: 'center', cellWidth: 45 } },
              { content: element.val().sexo, styles: { halign: 'center', cellWidth: 25 } },
              { content: element.val().cargo, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().email, styles: { halign: 'center', cellWidth: 48 } }
            ]
          ]
        });

      });
    });
    pdf.save('listaPersonal.pdf');
  }

  deletImg() {
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.imgSrc = '';
      this.fileUploader.nativeElement.value = "";
      this.personalService.selectedPersonal.perfil = "";
    } else {
      let img = (this.personalService.selectedPersonal.perfil).substring(89, 120);
      let img2 = (img).split("?");
      this.storage.ref('Upload/personal/' + img2[0]).delete();
    }
  }
}
