import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';


import { ClienteService } from '../../servicios/ser-cliente/cliente.service';
import { Cliente } from "../../models/cliente";
import { ToastrService } from "ngx-toastr"

import { element } from 'protractor';
import { isObject } from 'util';
import { NgForm } from '@angular/forms';
import { Observable } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { AngularFireStorage } from 'angularfire2/storage';
import * as firebase from 'firebase/app';
import * as jspdf from 'jspdf';
import { AuthService } from '../../servicios/ser-auth/auth.service';
import { Router } from '@angular/router';
declare var $: any;
@Component({
  selector: 'app-clientes',
  templateUrl: './clientes.component.html',
  styleUrls: ['./clientes.component.css']
})
export class ClientesComponent implements OnInit {
  uploadPercent: Observable<number>;
  urlImage: Observable<string>;
  nombreFoto1: any;
  file: any;
  filePath: any;
  selectedImage: any;
  ref: any;
  img: any;
  imgSrc: any;

  @ViewChild('fileUploader', { static: false }) fileUploader: ElementRef;

  clienteList: Cliente[];
  constructor(
    private clienteService: ClienteService,
    private toastr: ToastrService,
    private storage: AngularFireStorage,
    private authService: AuthService,
    private router: Router
  ) {
    this.nivel = true;
  }

  filterPost = '';

  onSubmit(clienteForm: NgForm) {
    this.img = (document.getElementById('urlImageValue') as HTMLInputElement).value;
    if (clienteForm.value.$key == null) {
      this.clienteService.selectedCliente.perfil = this.img;
      clienteForm.value.perfil = this.img;
      this.clienteService.insertCliente(clienteForm.value)
      this.toastr.success('Operacion Correcta', 'Cliente registrdo correctamente')
    }
    else {
      this.clienteService.selectedCliente.perfil = this.img;
      clienteForm.value.perfil = this.img;
      this.clienteService.updateCliente(clienteForm.value);
      this.toastr.success('Operacion Correcta', 'Cliente modificado correctamente');
    }
    this.resetForm(clienteForm);
    $('#exampleModalScrollable').modal('hide');
  }

  resetForm(clienteForm?: NgForm) {
    if (clienteForm != null)
      clienteForm.reset();
    this.clienteService.selectedCliente = new Cliente();
    this.imgSrc = '';
    this.fileUploader.nativeElement.value = "";
  }

  nivel = true;
  ngOnInit() {
    this.clienteService.getCliente()
      .snapshotChanges()
      .subscribe(item => {
        this.clienteList = [];
        item.forEach(element => {
          let x = element.payload.toJSON();
          x["$key"] = element.key;
          this.clienteList.push(x as Cliente);
        });
      });
  }
  
  onEdit(cliente: Cliente) {
    this.imgSrc = cliente.perfil;
    this.clienteService.selectedCliente = Object.assign({}, cliente);
  }

  onDelete($key: string) {
    let img;
    if (confirm('Esta seguro que desea eliminar?...')) {
      let refImg = firebase.database().ref('cliente');
      refImg.orderByKey().equalTo($key).once('child_added', snap => {
        img = (snap.val().perfil).substring(88, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/cliente/' + img2[0]).delete();
        this.clienteService.deleteCliente($key);
      })
      this.toastr.success('Operacion Correcta', 'Cliente eliminado correctamente')
    }

  }
  uploadFile1(event: any) {
    // limpiamos la muestra de la imagen pequeña
    this.imgSrc = '';
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.cargandoImagen(event);

    } else {
      this.cargandoImagen(event);
      if (this.clienteService.selectedCliente.perfil != undefined) {
        let img = (this.clienteService.selectedCliente.perfil).substring(89, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/personal/' + img2[0]).delete();
      }
    }
  }

  cargandoImagen(event: any) {
    // nombre aleatorio para la imagen
    const id = Math.random().toString(36).substring(2);
    this.file = event.target.files[0];
    this.filePath = 'Upload/cliente/perfilCliente_' + id;
    this.ref = this.storage.ref(this.filePath);
    const task = this.storage.upload(this.filePath, this.file);
    this.uploadPercent = task.percentageChanges();
    task.snapshotChanges().pipe(finalize(() => this.urlImage = this.ref.getDownloadURL())).subscribe();
    // cargar muestra de imagen pequeña
    const reader = new FileReader();
    reader.onload = (e: any) => this.imgSrc = e.target.result;
    reader.readAsDataURL(event.target.files[0]);
    this.selectedImage = event.target.files[0];
  }

  generarReporte() {
    var pdf = new jspdf({
      orientation: 'landscape',
      // unit: 'cm',
      format: 'carta'
    })
    let imgData = 'data:image/jpeg;base64,'
    // pdf.addImage(imgData, 'PNG', 7, 0, 38, 33);
    pdf.text("LISTA DE CLIENTES", (279.4 / 2.5), 13);
    // pdf.fromHTML(div, 10, 30);
    pdf.autoTable({
      margin: { top: 25 },
      head: [[
        { content: 'Nombre', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'Apellido', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'C.I.', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Expedido', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'Telefono', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'Direccion', styles: { halign: 'center', cellWidth: 55 } },
        { content: 'Sexo', styles: { halign: 'center', cellWidth: 20 } },
        { content: 'Email', styles: { halign: 'center', cellWidth: 50 } },
      ]]
    })
    var ref = firebase.database().ref('cliente');
    ref.orderByChild('detalle').on('value', snap => {
      snap.forEach(element => {
        // console.log(element.val().nombre);

        pdf.autoTable({
          // head: [['Nombre', 'Apellido', 'C.I.', 'Expedido', 'Fecha N.', 'Telefono', 'Direccion', 'Sexo', 'Cargo', 'Correo']],
          // head: [['Nombre']],//,'Apellido','C.I.','Fecha N.','Telefono','Direccion','Sexo','Cargo','Correo']],
          body: [
            [
              { content: element.val().nombre, styles: { halign: 'center', cellWidth: 30 } },
              { content: element.val().apellido, styles: { halign: 'center', cellWidth: 30 } },
              { content: element.val().ci, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().expedicion, styles: { halign: 'center', cellWidth: 30 } },
              { content: element.val().telefono, styles: { halign: 'center', cellWidth: 30 } },
              { content: element.val().direccion, styles: { halign: 'center', cellWidth: 55 } },
              { content: element.val().sexo, styles: { halign: 'center', cellWidth: 20 } },
              { content: element.val().email, styles: { halign: 'center', cellWidth: 50 } },
            ]
          ]
        });

      });
    });
    pdf.save('listaInsumos.pdf');
  }

  deletImg() {
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.imgSrc = '';
      this.fileUploader.nativeElement.value = "";
      this.clienteService.selectedCliente.perfil = "";
    } else {
      let img = (this.clienteService.selectedCliente.perfil).substring(89, 120);
      let img2 = (img).split("?");
      this.storage.ref('Upload/personal/' + img2[0]).delete();
    }
  }

}

