import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
// servicio
import { InsumoService } from '../../servicios/ser-insumo/insumo.service'
import { Insumo } from "../../models/insumo";
import { ToastrService } from "ngx-toastr";
import { NgForm } from '@angular/forms';
import { AngularFireStorage } from 'angularfire2/storage';
declare var $: any;
import { finalize } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { AlmacenService } from '../../servicios/ser-almacen/almacen.service';
import * as jspdf from 'jspdf';
import * as firebase from 'firebase/app';
import { AuthService } from '../../servicios/ser-auth/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-insumos',
  templateUrl: './insumos.component.html',
  styleUrls: ['./insumos.component.css']
})
export class InsumosComponent implements OnInit {
  uploadPercent: Observable<number>;
  urlImage: Observable<string>;

  insumoList: Insumo[];
  nombreFoto1: any;
  file: any;
  filePath: any;
  selectedImage: any;
  ref: any;
  img: any;
  imgSrc: any;

  @ViewChild('fileUploader', { static: false }) fileUploader: ElementRef;

  public almacen: any = { nombre: '', direccion: '' };
  constructor(private insumoService: InsumoService,
    private almacenService: AlmacenService,
    private storage: AngularFireStorage,
    private toastr: ToastrService,
    private authService: AuthService,
    private router: Router
  ) { }

  filterPost = '';

  onSubmit(insumoForm: NgForm) {
    this.img = (document.getElementById('urlImageValue') as HTMLInputElement).value;

    if (insumoForm.value.$key == null) {
      this.insumoService.selectedInsumo.imagen = this.img;
      insumoForm.value.imagen = this.img;
      let detalle = insumoForm.value.detalle;
      let obs = insumoForm.value.obs;
      let unidadMedida = insumoForm.value.unidadMedida;

      let desCorta = detalle + ', ' + obs + ', ' + unidadMedida;

      let estado = '';
      if (insumoForm.value.cantidadInsumo < 10) {
        estado = 'Poco';
      } else if (insumoForm.value.cantidadInsumo > 10) {
        estado = 'Lleno';
      } else {
        estado = 'Vacio';
      }

      
      this.almacenService.insertAlmacen(null, desCorta, 0, 0, 0, estado)
      

      

      this.insumoService.insertInsumo(insumoForm.value);
      this.toastr.success('Operacion Correcta', 'Insumo registrado correctamente')
    } else {
      this.insumoService.selectedInsumo.imagen = this.img;
      insumoForm.value.imagen = this.img;
      this.insumoService.updateInsumo(insumoForm.value);
      this.toastr.success('Operacion Correcta', 'Insumo modificado correctamente')
    }
    this.resetForm(insumoForm);
    $('#exampleModalScrollable').modal('hide');
  }
  resetForm(insumoForm?: NgForm) {
    if (insumoForm != null) {
      insumoForm.reset();
    }
    this.insumoService.selectedInsumo = new Insumo();
    this.imgSrc = '';
    this.fileUploader.nativeElement.value = "";
  }

  ngOnInit() {
    if (this.authService.correo === null || this.authService.correo === undefined) {
      this.router.navigate(['/autentificacion']);
    }

    this.almacenService.getAlmacen()
    this.insumoService.getInsumo()
      .snapshotChanges()
      .subscribe(item => {
        this.insumoList = [];
        item.forEach(element => {
          const x = element.payload.toJSON();
          x['$key'] = element.key;
          this.insumoList.push(x as Insumo);
        });


      });
  }

  onEdit(insumo: Insumo) {
    this.imgSrc = insumo.imagen;
    this.insumoService.selectedInsumo = Object.assign({}, insumo);
  }

  onDelete($key: string) {
    let img;
    if (confirm('Esta seguro que desea eliminar?...')) {
      this.insumoService.deleteInsumo($key);
      let refImg = firebase.database().ref('insumos');
      refImg.orderByKey().equalTo($key).on('child_added', snap => {
        img = (snap.val().imagen).substring(89, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/insumos/' + img2[0]).delete();
      })
      this.toastr.success('Operacion Correcta', 'Insumo eliminado correctamente')
    }

  }
  uploadFile1(event: any) {
    // limpiamos la muestra de la imagen pequeña
    this.imgSrc = '';
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.cargandoImagen(event);

    } else {
      this.cargandoImagen(event);
      if (this.insumoService.selectedInsumo.imagen != undefined) {
        let img = (this.insumoService.selectedInsumo.imagen).substring(89, 120);
        let img2 = (img).split("?");
        this.storage.ref('Upload/insumos/' + img2[0]).delete();
      }
    }
  }

  cargandoImagen(event: any) {
    // nombre aleatorio para la imagen
    const id = Math.random().toString(36).substring(2);
    this.file = event.target.files[0];
    this.filePath = 'Upload/insumos/insumos_' + id;
    this.ref = this.storage.ref(this.filePath);
    const task = this.storage.upload(this.filePath, this.file);
    this.uploadPercent = task.percentageChanges();
    task.snapshotChanges().pipe(finalize(() => this.urlImage = this.ref.getDownloadURL())).subscribe();
    // cargar muestra de imagen pequeña
    const reader = new FileReader();
    reader.onload = (e: any) => this.imgSrc = e.target.result;
    reader.readAsDataURL(event.target.files[0]);
    this.selectedImage = event.target.files[0];
  }

  generarReporte() {
    var pdf = new jspdf({
      orientation: 'landscape',
      // unit: 'cm',
      format: 'carta'
    })
    let imgData = 'data:image/jpeg;base64,'
    // pdf.addImage(imgData, 'JPEG', 7, 0, 38, 33);
    pdf.text("LISTA DE PRODUCTOS", (279.4 / 2.5), 13);
    // pdf.fromHTML(div, 10, 30);
    pdf.autoTable({
      margin: { top: 25 },
      head: [[
        { content: 'Detalle', styles: { halign: 'center', cellWidth: 50 } },
        { content: 'Unidad de Medida', styles: { halign: 'center', cellWidth: 50 } },
        { content: 'Color', styles: { halign: 'center', cellWidth: 50 } },
        { content: 'Observacion', styles: { halign: 'center', cellWidth: 105 } },
      ]]
    })
    var ref = firebase.database().ref('Insumo');
    ref.orderByChild('detalle').on('value', snap => {
      snap.forEach(element => {
        // console.log(element.val().nombre);

        pdf.autoTable({
          // head: [['Nombre', 'Apellido', 'C.I.', 'Expedido', 'Fecha N.', 'Telefono', 'Direccion', 'Sexo', 'Cargo', 'Correo']],
          // head: [['Nombre']],//,'Apellido','C.I.','Fecha N.','Telefono','Direccion','Sexo','Cargo','Correo']],
          body: [
            [
              { content: element.val().detalle, styles: { halign: 'center', cellWidth: 50 } },
              { content: element.val().unidadMedida, styles: { halign: 'center', cellWidth: 50 } },
              { content: element.val().color, styles: { halign: 'center', cellWidth: 50 } },
              { content: element.val().obs, styles: { halign: 'center', cellWidth: 105 } },
            ]
          ]
        });

      });
    });
    pdf.save('listaInsumos.pdf');
  }

  deletImg() {
    if (this.filePath != undefined) {
      this.storage.ref(this.filePath).delete();
      this.imgSrc = '';
      this.fileUploader.nativeElement.value = "";
      this.insumoService.selectedInsumo.imagen = "";
    } else {
      let img = (this.insumoService.selectedInsumo.imagen).substring(89, 120);
      let img2 = (img).split("?");
      this.storage.ref('Upload/insumos/' + img2[0]).delete();
    }
  }

}
