import { Component, OnInit, Input } from '@angular/core';
import { ItemRegistroService } from '../../servicios/ser-item_registro/item-registro.service'
import { ItemRegistro } from "../../models/item-registro";
import { ToastrService } from "ngx-toastr";
import { NgForm } from '@angular/forms';
declare var $: any;
import { Observable } from 'rxjs';
import { finalize, timeout } from 'rxjs/operators';
import { AngularFireStorage } from 'angularfire2/storage';
import { ClienteService } from '../../servicios/ser-cliente/cliente.service';
import { Cliente } from '../../models/cliente';
import { element } from 'protractor';
import * as firebase from 'firebase/app';
import { AngularFireDatabase } from 'angularfire2/database';
import { AuthService } from '../../servicios/ser-auth/auth.service';
import { Catalogo } from '../../models/catalogo';
import { CatalogoService } from '../../servicios/ser-catalogo/catalogo.service';
import * as jspdf from 'jspdf';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cotizacion',
  templateUrl: './cotizacion.component.html',
  styleUrls: ['./cotizacion.component.css']
})
export class CotizacionComponent implements OnInit {

  uploadPercent: Observable<number>;
  urlImage: Observable<string>;
  nombreFoto1: any;
  file: any;
  filePath: any;
  selectedImage: any;
  ref: any;
  img: any;
  imgSrc: any;
  ci: any;
  nombreCliente: any;
  apCliente: any;
  cedulaIdentidadCliente: any;
  correoCliente: any;

  @Input() ciCliente;

  itemRegistroList: ItemRegistro[];
  clienteList: Cliente[];
  catalogoList: Catalogo[];
  constructor(
    private itemRegistroService: ItemRegistroService,
    private cliente: ClienteService,
    private toastr: ToastrService,
    private storage: AngularFireStorage,
    private db: AngularFireDatabase,
    private authService: AuthService,
    private catalogoService: CatalogoService,
    private router: Router
  ) {
  }
  filterPost = '';
  a: number;
  b: number;
  s: number;

  onSubmit(itemRegistroForm: NgForm) {
    this.img = (document.getElementById('urlImageValue') as HTMLInputElement).value;
    itemRegistroForm.value.idRegistro = (document.getElementById('idRegistro') as HTMLInputElement).value;

    if (itemRegistroForm.value.$key == null) {
      itemRegistroForm.value.modelo = this.img
      this.itemRegistroService.insertItemRegistro(itemRegistroForm.value)
      this.toastr.success('Operacion Correcta', 'Item Pedido registrdo correctamente')
    }
    else {
      this.itemRegistroService.updateItemRegistro(itemRegistroForm.value);
      this.toastr.success('Operacion Correcta', 'Item Pedido modificado correctamente')
    }
    this.resetForm(itemRegistroForm);
    $('#exampleModalScrollable').modal('hide');
  }
  resetForm(itemRegistroForm?: NgForm) {
    if (itemRegistroForm != null)
      itemRegistroForm.reset();
    this.itemRegistroService.selectedItemRegistro = new ItemRegistro();
  }

  catList: {
    descripcion: any,
    tipo: any,
    precio: any,
    descuento: any,
    tAcabado: any,
    imagen1: any,
    cantidad: any,
    costoTotal: any,
    precioDescuento: any,
    costoDescuento: any
  }[];

  totalCantidad: any;
  totalPagar: any;
  totalDescuento: any;
  costoTotalDescuento: any;

  correo: any;
  cedula: any;
  nombre: any;
  apellido: any;
  ngOnInit() {
    if (this.authService.correo === null || this.authService.correo === undefined) {
      this.router.navigate(['/autentificacion']);
    }
    this.totalPagar = 0;
    this.totalCantidad = 0;
    this.totalDescuento = 0;
    this.costoTotalDescuento = 0;

    this.correo = this.authService.correo;
    // this.correo = 'cliente@gmail.com';
    let refCliente = firebase.database().ref('cliente');
    let refCotizacion = firebase.database().ref('cotizacion');
    let refCatalogo = firebase.database().ref('Catalogo');

    refCliente.orderByChild('email').equalTo(this.correo).once('child_added', snap => {
      this.cedula = snap.val().ci;
      this.nombre = snap.val().nombre;
      this.apellido = snap.val().apellido;

      refCotizacion.orderByChild('cedula').equalTo(this.cedula).on('child_added', snapCotizacion => {
        // console.log(snapCotizacion.val().llavePrenda);
        // console.log(snapCotizacion.val().cantidad);

        this.catList = [];
        this.catalogoService.getCatalogo().snapshotChanges().subscribe(item => {
          item.forEach(element => {
            let x = element.payload.toJSON();
            x["$key"] = element.key;
            // console.log(x["$key"]);
            // console.log("snap: "+snapCotizacion.val().llavePrenda);
            let cantCero: number = 10;
            let descuento = parseInt(x["descuento"]);

            for (let des of x["descuento"]) {
              cantCero = cantCero * 10;
            }
            if (cantCero == 0) {
              cantCero = 1;
            }






            if (x["$key"] == snapCotizacion.val().llavePrenda) {
              this.catList.push({
                descripcion: x["descripcion"],
                tipo: x["tipo"],
                precio: x["precio"],
                descuento: x["descuento"],
                tAcabado: x["tAcabado"],
                imagen1: x["imagen1"],
                cantidad: snapCotizacion.val().cantidad,
                costoTotal: parseInt(x["precio"]) * parseInt(snapCotizacion.val().cantidad),
                precioDescuento: parseInt(x["precio"]) - (parseInt(x["precio"]) * (x["descuento"] / (cantCero / 10))),
                costoDescuento: ((parseInt(x["precio"]) - (parseInt(x["precio"]) * (x["descuento"] / (cantCero / 10)))) * snapCotizacion.val().cantidad)
              })
              this.totalCantidad = this.totalCantidad + parseInt(snapCotizacion.val().cantidad);
              this.totalPagar = this.totalPagar + (parseInt(x["precio"]) * parseInt(snapCotizacion.val().cantidad));
              this.totalDescuento = this.totalDescuento + (parseInt(x["precio"]) - (parseInt(x["precio"]) * (x["descuento"] / (cantCero / 10))))
              this.costoTotalDescuento = this.costoTotalDescuento + ((parseInt(x["precio"]) - (parseInt(x["precio"]) * (x["descuento"] / (cantCero / 10)))) * snapCotizacion.val().cantidad);
            }
          });
        });

      });
    });
  }

  onEdit(insumo: ItemRegistro) {
    this.itemRegistroService.selectedItemRegistro = Object.assign({}, insumo);
  }

  onDelete($key: string) {
    if (confirm('Esta seguro que desea eliminar?...')) {
      this.itemRegistroService.deleteItemRegistro($key);
      this.toastr.success('Operacion Correcta', 'Item Registro eliminado correctamente')
    }

  }

  uploadFile1(event: any) {
    // limpiamos la muestra de la imagen pequeña
    this.imgSrc = '';
    // nombre aleatorio para la imagen
    const id = Math.random().toString(36).substring(2);
    this.file = event.target.files[0];
    this.filePath = 'Upload/personal/perfilPersonal_' + id;
    this.ref = this.storage.ref(this.filePath);
    const task = this.storage.upload(this.filePath, this.file);
    this.uploadPercent = task.percentageChanges();
    task.snapshotChanges().pipe(finalize(() => this.urlImage = this.ref.getDownloadURL())).subscribe();
    // cargar muestra de imagen pequeña
    const reader = new FileReader();
    reader.onload = (e: any) => this.imgSrc = e.target.result;
    reader.readAsDataURL(event.target.files[0]);
    this.selectedImage = event.target.files[0];
  }

  cedulaCliente(evento) {
    this.itemRegistroService.getItemRegistro()
      .snapshotChanges()
      .subscribe(item => {
        this.itemRegistroList = [];
        item.forEach(element => {
          let x = element.payload.toJSON();
          x["$key"] = element.key;
          if (x["idRegistro"] == evento.target.value) {
            this.itemRegistroList.push({
              $key: x['$key'],
              idRegistro: x['idRegistro'],
              prenda: x['prenda'],
              cantidad: x['cantidad'],
              costoUnitario: x['costoUnitario'],
              costoTotal: x['costoTotal'],
              modelo: x['modelo'],
              color: x['color'],
            });
          }
        });
      });
  }

  datosCatalogo: {
    idRegistro,
    tipoPrenda,
    cantidad,
    costoUnitario,
    costoTotal,
    modelo,
    color
  }[];

  buscarCliente(ciCliente) {
    var ref = firebase.database().ref('cliente');
    ref.orderByChild('ci').equalTo(ciCliente.value).on('child_added', snap => {
      this.nombreCliente = snap.val().nombre;
      this.apCliente = snap.val().apellido;
      this.cedulaIdentidadCliente = snap.val().ci;
      this.correoCliente = snap.val().email;
    });

  }

  generarReporte() {
    var pdf = new jspdf({
      orientation: 'landscape',
      format: 'carta'
    });
    let imgData = 'data:image/jpeg;base64,'
    pdf.text("COTIZACION", (279.4 / 2.5), 13);
    // pdf.addImage(imgData, 'PNG', 7, 0, 38, 33);
    pdf.setFontSize(11);
    pdf.text('Nombre: ' + this.nombre, 50, 22);
    pdf.text('Apellido: ' + this.apellido, 125, 22);
    pdf.text('Cedula Identidad: ' + this.cedula, 200, 22);

    pdf.autoTable({
      margin: { top: 25 },
      head: [[
        { content: 'Tipo de Prenda', styles: { halign: 'center', cellWidth: 50 } },
        { content: 'Costo Unitario', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'Descuento', styles: { halign: 'center', cellWidth: 30 } },
        { content: 'Cantidad', styles: { halign: 'center', cellWidth: 55 } },
        { content: 'Costo Total', styles: { halign: 'center', cellWidth: 33 } },
        { content: 'Precio Descuento', styles: { halign: 'center', cellWidth: 33 } },
        { content: 'Costo Total Descuento', styles: { halign: 'center', cellWidth: 33 } }
      ]]
    });
    this.catList.forEach(element => {
      pdf.autoTable({
        body: [
          [
            { content: element.tipo, styles: { halign: 'center', cellWidth: 50 } },
            { content: element.precio, styles: { halign: 'center', cellWidth: 30 } },
            { content: element.descuento, styles: { halign: 'center', cellWidth: 30 } },
            { content: element.cantidad, styles: { halign: 'center', cellWidth: 55 } },
            { content: element.costoTotal, styles: { halign: 'center', cellWidth: 33 } },
            { content: element.precioDescuento, styles: { halign: 'center', cellWidth: 33 } },
            { content: element.costoDescuento, styles: { halign: 'center', cellWidth: 33 } },
          ]
        ]
      });
    });

    pdf.autoTable({
      body: [
        [
          { content: 'TOTAL', styles: { halign: 'center', cellWidth: 50, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '', styles: { halign: 'center', cellWidth: 30, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '', styles: { halign: 'center', cellWidth: 30, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '' + this.totalCantidad, styles: { halign: 'center', cellWidth: 55, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '' + this.totalPagar, styles: { halign: 'center', cellWidth: 33, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '' + this.totalDescuento, styles: { halign: 'center', cellWidth: 33, fillColor: [41, 128, 186], textColor: [255, 255, 255] } },
          { content: '' + this.costoTotalDescuento, styles: { halign: 'center', cellWidth: 33, fillColor: [41, 128, 186], textColor: [255, 255, 255] } }
        ]
      ]
    });
    pdf.save('cotizacion.pdf');
  }
}
