import { Component, OnInit } from '@angular/core';


// servicio
import {ProveedorService} from '../../servicios/ser-proveedor/proveedor.service'
import {Proveedor} from "../../models/proveedor";
import {ToastrService} from "ngx-toastr";
import { NgForm } from '@angular/forms';
import * as firebase from 'firebase/app';
import * as jspdf from 'jspdf';
import { AuthService } from '../../servicios/ser-auth/auth.service';
import { Router } from '@angular/router';
declare var $:any;

@Component({
  selector: 'app-proveedores',
  templateUrl: './proveedores.component.html',
  styleUrls: ['./proveedores.component.css']
})
export class ProveedoresComponent implements OnInit {
  proveedorList:Proveedor[];
  constructor(
    private proveedorService:ProveedorService,
     private toastr:ToastrService,
     private authService:AuthService,
     private router:Router
  ) { }


 filterPost='';
 
  onSubmit(proveedorForm:NgForm)
  {
    if(proveedorForm.value.$key==null)
    {
     this.proveedorService.insertProveedor(proveedorForm.value)
     this.toastr.success('Operacion Correcta','Proveedor registrdo correctamente')
    }
     else
    {
    this.proveedorService.updateProveedor(proveedorForm.value);
    this.toastr.success('Operacion Correcta','Proveedor modificado correctamente')
    }
  this.resetForm(proveedorForm);
  }
  resetForm(proveedorForm?: NgForm)
  {
    if (proveedorForm != null)
    proveedorForm.reset();
    this.proveedorService.selectedProveedor=new Proveedor();
  }

  ngOnInit() {
    if(this.authService.correo === null  || this.authService.correo === undefined){
      this.router.navigate(['/autentificacion']);
    }
    
    this.proveedorService.getProveedor()
    .snapshotChanges()
    .subscribe(item=>{
      this.proveedorList=[];
      item.forEach(element=>{let x = element.payload.toJSON();
        x["$key"] = element.key;
        this.proveedorList.push(x as Proveedor);
      });
     

    });
  }
  onEdit(proveedor:Proveedor)
  {
    this.proveedorService.selectedProveedor=Object.assign({}, proveedor);
  }

   onDelete($key:string)
   {
     if (confirm('Esta seguro que desea eliminar?...'))
     {
      this.proveedorService.deleteProveedor($key);
      this.toastr.success('Operacion Correcta','Proveedor eliminado correctamente')
     }
  
    }

    generarReporte() {
      var pdf = new jspdf({
        orientation: 'landscape',
        // unit: 'cm',
        format: 'carta'
      })
      let imgData = 'data:image/jpeg;base64,'
      // pdf.addImage(imgData, 'PNG', 7, 0, 38, 33);
      pdf.text("LISTA DE PROVEEDORES", (279.4 / 2.5), 13);
      // pdf.fromHTML(div, 10, 30);
      pdf.autoTable({
        margin: { top: 25 },
        head: [[
          { content: 'Nombre', styles: { halign: 'center', cellWidth: 35 } },
          { content: 'NIT', styles: { halign: 'center', cellWidth: 20 } },
          { content: 'Direccion', styles: { halign: 'center', cellWidth: 45 } },
          { content: 'Telefono', styles: { halign: 'center', cellWidth: 20 } },
          { content: 'Ciudad', styles: { halign: 'center', cellWidth: 40 } },
          { content: 'Email', styles: { halign: 'center', cellWidth: 40 } },
          { content: 'Direccion Web', styles: { halign: 'center', cellWidth: 55 } },
        ]]
      })
      var ref = firebase.database().ref('Proveedor');
      ref.orderByChild('detalle').on('value', snap => {
        snap.forEach(element => {
          // console.log(element.val().nombre);
  
          pdf.autoTable({
            // head: [['Nombre', 'Apellido', 'C.I.', 'Expedido', 'Fecha N.', 'Telefono', 'Direccion', 'Sexo', 'Cargo', 'Correo']],
            // head: [['Nombre']],//,'Apellido','C.I.','Fecha N.','Telefono','Direccion','Sexo','Cargo','Correo']],
            body: [
              [
                { content: element.val().nombre, styles: { halign: 'center', cellWidth: 35 } },
                { content: element.val().nit, styles: { halign: 'center', cellWidth: 20 } },
                { content: element.val().direccion, styles: { halign: 'center', cellWidth: 45 } },
                { content: element.val().telefono, styles: { halign: 'center', cellWidth: 20 } },
                { content: element.val().city, styles: { halign: 'center', cellWidth: 40 } },
                { content: element.val().email, styles: { halign: 'center', cellWidth: 40 } },
                { content: element.val().durl, styles: { halign: 'center', cellWidth: 55 } },
              ]
            ]
          });
  
        });
      });
      pdf.save('listaInsumos.pdf');
    }
   

}


